import Vue from "vue";
import Vuex from "vuex";
import clone from "@/lib/clone";
import createId from "@/lib/createId";
import router from "@/router";
import { Message } from "element-ui";

Vue.use(Vuex);
const payIconList = [
  { iconName: "child" },
  { iconName: "cure" },
  { iconName: "dress" },
  { iconName: "fruit" },
  { iconName: "live" },
  { iconName: "repast" },
  { iconName: "sports" },
  { iconName: "traffic" },
  { iconName: "bicycle" },
  { iconName: "billiards" },
  { iconName: "book" },
  { iconName: "bowling" },
  { iconName: "game" },
  { iconName: "house" },
  { iconName: "smartphone" },
  { iconName: "swim" },
  { iconName: "taxi" },
  { iconName: "tea" },
];
const receiptIconList = [
  { iconName: "bill" },
  { iconName: "creditCard" },
  { iconName: "donation" },
  { iconName: "piggyBank" },
  { iconName: "purse" },
  { iconName: "redEnvelope" },
  { iconName: "revenue" },
];

const store = new Vuex.Store({
  state: {
    recordList: [],
    createRecordError: null,
    tagList: [],
    payIconList: payIconList,
    receiptIconList: receiptIconList,
    currentTag: undefined,
    currentRecord: undefined,
  } as RootState,
  mutations: {
    setCurrentTag(state, id: string) {
      state.currentTag = state.tagList.filter((t) => t.id === id)[0];
    },
    setCurrentRecord(state, id: string) {
      state.currentRecord = state.recordList.filter((r) => r.id === id)[0];
    },
    updateTag(state, payload: { id: string; name: string }) {
      const { id, name } = payload;
      const idList = state.tagList.map((item) => item.id);
      if (idList.indexOf(id) >= 0) {
        const names = state.tagList.map((item) => item.name);
        if (names.indexOf(name) >= 0) {
          Message.warning({
            message: "标签名重复,请重新输入",
            offset: 55,
            showClose: true,
          });
        } else {
          const tag = state.tagList.filter((item) => item.id === id)[0];
          tag.name = name;
          store.commit("saveTags");
        }
      }
    },
    updateRecordAmount(state, payload: { id: string; amount: number }) {
      const { id, amount } = payload;
      const idList = state.recordList.map((item) => item.id);
      if (idList.indexOf(id) >= 0) {
        const record = state.recordList.filter((item) => item.id === id)[0];
        record.amount = amount;
        store.commit("saveRecords");
      }
    },
    updateRecordNotes(state, payload: { id: string; notes: string }) {
      const { id, notes } = payload;
      const idList = state.recordList.map((item) => item.id);
      if (idList.indexOf(id) >= 0) {
        const record = state.recordList.filter((item) => item.id === id)[0];
        record.notes = notes;
        store.commit("saveRecords");
      }
    },
    removeTag(state, id: string) {
      let index = -1;
      for (let i = 0; i < state.tagList.length; i++) {
        if (state.tagList[i].id === id) {
          index = i;
          break;
        }
      }
      if (index >= 0) {
        state.tagList.splice(index, 1);
        store.commit("saveTags");
        router.back();
      } else {
        window.alert("删除失败");
      }
    },
    removeRecord(state, id: string) {
      let index = -1;
      for (let i = 0; i < state.recordList.length; i++) {
        if (state.recordList[i].id === id) {
          index = i;
          break;
        }
      }
      if (index >= 0) {
        state.recordList.splice(index, 1);
        store.commit("saveRecords");
        router.back();
      } else {
        window.alert("删除失败");
      }
    },
    fetchRecords(state) {
      state.recordList = JSON.parse(
        window.localStorage.getItem("recordList") || "[]"
      ) as RecordItem[];
    },
    createRecord(state, record: RecordItem) {
      const record2 = clone(record);
      const id = createId().toString();
      record2.createdAt = record2.createdAt || new Date().toISOString();
      record2.id = id;
      state.recordList.push(record2);
      store.commit("saveRecords");
    },
    saveRecords(state) {
      window.localStorage.setItem(
        "recordList",
        JSON.stringify(state.recordList)
      );
    },
    fetchTags(state) {
      state.tagList = JSON.parse(
        window.localStorage.getItem("tagList") || "[]"
      );
      if (!state.tagList || state.tagList.length === 0) {
        store.commit("createTag", { name: "服饰", icon: "dress" });
        store.commit("createTag", { name: "孩子", icon: "child" });
        store.commit("createTag", { name: "水果", icon: "fruit" });
        store.commit("createTag", { name: "住宿", icon: "live" });
        store.commit("createTag", { name: "运动", icon: "sports" });
      }
    },
    createTag(state, tag: { name: string; icon: string }) {
      const { name, icon } = tag;
      const names = state.tagList.map((item) => item.name);
      if (names.indexOf(name) < 0) {
        const id = createId().toString();
        state.tagList.push({ id, name, icon });
        store.commit("saveTags");
      }
    },
    saveTags(state) {
      window.localStorage.setItem("tagList", JSON.stringify(state.tagList));
    },
  },
});

export default store;
