





























import Vue from "vue";
import FormItem from "@/components/Money/FormItem.vue";
import Button from "@/components/Button.vue";
import { Component } from "vue-property-decorator";
import { Message } from "element-ui";

@Component({
  components: { FormItem, Button },
})
export default class EditLabel extends Vue {
  get currentRecord() {
    return this.$store.state.currentRecord;
  }
  get currentTag() {
    return this.$store.state.currentTag;
  }
  get recordList() {
    return this.$store.state.recordList;
  }
  created() {
    const id = this.$route.params.id;
    this.$store.commit("fetchRecords");
    this.$store.commit("setCurrentRecord", id);
    const tagId = this.currentRecord.tags[0].id;
    this.$store.commit("fetchTags");
    this.$store.commit("setCurrentTag", tagId);
    if (!this.currentRecord) {
      this.$router.replace("/404");
    }
  }
  updateAmount(amount: number) {
    if (this.currentRecord) {
      this.$store.commit("updateRecordAmount", {
        id: this.currentRecord.id,
        amount,
      });
    }
  }
  updateNotes(notes: string) {
    if (this.currentRecord) {
      this.$store.commit("updateRecordNotes", {
        id: this.currentRecord.id,
        notes,
      });
    }
  }
  remove() {
    if (this.currentRecord) {
      this.$store.commit("removeRecord", this.currentRecord.id);
    }
  }
  goBack() {
    const number = Number(this.currentRecord.amount);
    if (Number.isNaN(number)) {
      return Message.warning({
        message: "金额只能为数字，请重新输入",
        offset: 55,
        showClose: true,
      });
    } else {
      this.$router.back();
    }
  }
}
